:root{
  --primary: #27a41c;
  --secondary: #556BA5;
  --dark: #dedb1667;
  --light: #4622d4;
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Fira Sans', sans-serif;

}
body{
  background-color: var(--light);
}
.App{
  display: flex;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
  padding: 16px;

}
.calculator{
  width: 100%;
  max-width: 400px;
  background-color: rgb(208, 209, 211);
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0px 2px 64px rgba(0, 0, 0, 0.2);


}
.display{
  padding: 16px;
  text-align: right;
  background-color: var(--primary);
  color: var(--light);
  font-size: 24px;
  font-weight: 300;

}

.display span{
  font-size: 14px;
  color: var(--light);
}
button{
  appearance: none;
  border: none;
  outline: none;
  color: rgb(83, 27, 212);
  font-size: 20px;
  padding: 16px;
  cursor: pointer;
  transition: 0.4s;

}
button:hover {
  opacity: 0.9;
}
.operators{
  display: flex;
}
.operators button{
  flex: 1 1 0%;
  background-color: var(--secondary);
  font-weight: 800;
}
.digits{
  display: flex;
  flex-wrap: wrap;

}
.digits button{
  flex: 1 1 33.333%;
  max-width: 33.333%;
  background-color: var(--dark);

}

